.nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 8vh;
  /* background: transparent; */
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  /* padding: 4rem 5rem; */
  z-index: 100;
  width: 100%;
  margin-bottom: 10px;
  /* box-shadow: 0px 2px 5px rgba(221, 221, 221, 0.5); */
}
.nav__scroll {
  background-color: white;
  box-shadow: 0px 2px 5px rgba(221, 221, 221, 0.5);
}
.logo{
  width: 180px;
  height: 40px;
}

.nav__menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  grid-gap: 3rem;
  gap: 3rem;
}
.nav__toggler {
  display: none;
}
.nav__toggler div {
  width: 2.5rem;
  height: 0.2rem;
  margin: 0.4rem;
  background: #f5c104;
  transition: 0.3s ease-in;
}


@media screen and (max-width: 768px) {
  .nav__toggler {
    display: block;
    cursor: pointer;
  }
  .nav__menu {
    position: fixed;
    top: 8vh;
    right: 0;
    height: 93vh;
    width: 50vw;
    background: rgb(241, 241, 241);
    flex-direction: column;
    transform: translateX(100%);
    transition: 0.5s ease-in;
  }
}
/* Active Class */
.nav__active {
  transform: translateX(0%);
}

/* Toggle Icon Animation */

.toggle .line1 {
  transform: rotate(-45deg) translate(-4px, 5px);
}
.toggle .line2 {
  opacity: 0;
}
.toggle .line3 {
  transform: rotate(45deg) translate(-4px, -5px);
}

