  .galleryItem img {
    width: 100%;
    height: 95%;
    border: 15px solid white;
    /* object-fit: cover; */
  }
  
  .galleryContainer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
    gap: 20px;
    /* border: 0.5px solid rgb(213, 213, 213); */
    /* box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px; */
    padding: 10px;
    background-color: #fffae7;
  }
  
  .galleryWrapper {
    padding: 5% 10%;
  }
  
  .filterItem ul {
    
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    padding: 0;
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 30px;
  }
  
  .filterItem ul button {
    background: #ffffff;
    /* border : 0.5px solid #d7d7d7; */
    /* box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px; */
    color: #f5c104;
    border: 1px solid #f5c104 ;
    padding: 8px 40px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
    border-radius: 50px;
  }
  
  .filterItem ul button:hover {
    color: #24262b;
    background-color: white;
    box-shadow: rgba(255, 187, 39, 0.35) 0px 5px 15px;
    cursor: pointer;
    border: none;
  }
  
  .galleryItem img {
    border-radius: 5px;
  }
 
@media (max-width:768px) {
    .galleryContainer{
        grid-template-columns: 1fr;
    }
    .filterItem ul {
        display: grid;
        grid-template-columns: 1fr 1fr;
 
    }

}

